@font-face {
    font-family: 'icomoon';
    src:  url('./assets/fonts/icomoon.eot?v5sgtj');
    src:  url('./assets/fonts/icomoon.eot?v5sgtj#iefix') format('embedded-opentype'),
      url('./assets/fonts/icomoon.ttf?v5sgtj') format('truetype'),
      url('./assets/fonts/icomoon.woff?v5sgtj') format('woff'),
      url('./assets/fonts/icomoon.svg?v5sgtj#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
  [class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  /* plus */
  .icon-plus:before {
    content: "\e902";
  }

  /* reload */
